/* -------------------------------------------------------------
   src/app/page.tsx
   Home page → hero ▸ animated category carousel ▸ featured products
--------------------------------------------------------------*/
'use client';

import { useState, useMemo } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronLeft, ChevronRight } from 'lucide-react';

import Hero from '../components/Hero';
import ProductCard from '../components/ProductCard';
import { allCategories } from '../lib/categories'; // client-safe helper

// ─────────────────────────────────────────────────────────────────────────────
// Config
// ─────────────────────────────────────────────────────────────────────────────
const CATS_PER_PAGE = 4; // show 4 categories at a time (tweakable)

export default function Home() {
  /* --------------------------------------------------------------------
        1.  DATA  – Featured products & (static) categories
  -------------------------------------------------------------------- */
  const products = [
    {
      title: 'Oak Dining Table',
      price: 45_000,
      img: '/assets/img/products/oak-table.webp',
      slug: 'oak-dining-table',
    },
    {
      title: 'Walnut Armchair',
      price: 28_000,
      img: '/assets/img/products/walnut-chair.webp',
      slug: 'walnut-armchair',
    },
  ];

  const [page, setPage] = useState(0);

  /** Break the categories into pages of CATS_PER_PAGE each */
  const pages = useMemo(() => {
    const chunks: typeof allCategories[][] = [];
    for (let i = 0; i < allCategories.length; i += CATS_PER_PAGE) {
      chunks.push(allCategories.slice(i, i + CATS_PER_PAGE));
    }
    return chunks;
  }, []);

  const next = () => setPage((p) => (p + 1) % pages.length);
  const prev = () => setPage((p) => (p - 1 + pages.length) % pages.length);

  /* --------------------------------------------------------------------
        2.  RENDER
  -------------------------------------------------------------------- */
  return (
    <>
      <Hero />

      {/* ─── Categories carousel ─────────────────────────────────────── */}
      <section className="relative mx-auto mt-16 max-w-6xl px-4">
        {/* Title */}
        <h2 className="mb-6 font-display text-2xl font-semibold text-walnut">
          Browse by Category
        </h2>

        <div className="relative overflow-hidden">
          {/* Arrow buttons – floated LEFT / RIGHT */}
          {pages.length > 1 && (
            <>
              <button
                aria-label="Previous"
                onClick={prev}
                className="absolute left-0 top-1/2 -translate-y-1/2 rounded-full bg-walnut/10 p-2 backdrop-blur transition hover:bg-walnut/20"
              >
                <ChevronLeft className="h-5 w-5 text-walnut" />
              </button>
              <button
                aria-label="Next"
                onClick={next}
                className="absolute right-0 top-1/2 -translate-y-1/2 rounded-full bg-walnut/10 p-2 backdrop-blur transition hover:bg-walnut/20"
              >
                <ChevronRight className="h-5 w-5 text-walnut" />
              </button>
            </>
          )}

          {/* Track */}
          <AnimatePresence initial={false} mode="wait">
            <motion.ul
              key={page}
              initial={{ x: 40, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              exit={{ x: -40, opacity: 0 }}
              transition={{ duration: 0.35, ease: 'easeOut' }}
              className="grid grid-cols-2 gap-6 sm:grid-cols-4"
            >
              {pages[page].map((cat) => (
                <li key={cat.slug} className="list-none">
                  <a
                    href={`/${cat.slug}`}
                    className="group relative block overflow-hidden rounded-xl"
                  >
                    {/* backdrop image */}
                    <img
                      src={cat.image}
                      alt={cat.title}
                      className="h-40 w-full object-cover transition duration-300 group-hover:scale-105"
                      loading="lazy"
                      width={400}
                      height={160}
                    />

                    {/* overlay tint */}
                    <span className="absolute inset-0 bg-black/20 transition group-hover:bg-black/30" />

                    {/* title */}
                    <span className="absolute inset-0 flex items-center justify-center font-display text-lg font-medium text-ivory drop-shadow">
                      {cat.title}
                    </span>
                  </a>
                </li>
              ))}
            </motion.ul>
          </AnimatePresence>
        </div>
      </section>

      {/* ─── Featured products grid ───────────────────────────────────── */}
      <section className="mx-auto my-16 grid max-w-6xl grid-cols-[repeat(auto-fill,minmax(260px,1fr))] gap-8 px-4">
        {products.map((p) => (
          <ProductCard key={p.slug} {...p} />
        ))}
      </section>
    </>
  );
}